input {
    height: 24px;
    width: 320px;
    margin-top: 5px;
    border: 1px solid orangered;
    border-radius: 6px;
    padding: 4px;
    transition: 0.5s;
}
input:hover {
    box-shadow: 0px 0px 4px red;
}
.languages div {
    display: inline-block;
    margin: 6px 6px 6px 0;
    padding: 4px;
    font-size: 14px;
    border: 1px solid;
    cursor: pointer;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    box-shadow: 0px 0px 4px white;
}