.main {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.logos {
  margin-top: 10px;
}
.logos img {
  margin-right: 15px;
  opacity: 0.9;
  transition: 0.5s;
}
.logos img:hover {
  opacity: 1;
}
strong {
  margin-bottom: 10px;
}
hr {
  width: 30%;
  color: white;
}
footer {
    font-size: 12px;
    margin-top: 10px;
}